'use client';

import { useSession } from 'next-auth/react';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';

export default function DashboardPage() {
  const { data: session, status } = useSession();
  const router = useRouter();

  useEffect(() => {
    if (status === 'unauthenticated') {
      router.push('/auth/login');
    }
  }, [status, router]);

  if (status === 'loading') {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary"></div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100 py-6">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white shadow rounded-lg p-6">
          <h1 className="text-2xl font-semibold text-gray-900">Welcome, {session?.user?.name}!</h1>
          <p className="mt-2 text-gray-600">This is your dashboard. You are logged in as {session?.user?.email}</p>
          
          <div className="mt-6 grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3">
            <div className="bg-primary bg-opacity-10 rounded-lg p-6">
              <h2 className="text-lg font-medium text-gray-900">Your Orders</h2>
              <p className="mt-2 text-gray-600">View and manage your orders</p>
            </div>
            
            <div className="bg-primary bg-opacity-10 rounded-lg p-6">
              <h2 className="text-lg font-medium text-gray-900">Profile Settings</h2>
              <p className="mt-2 text-gray-600">Update your profile information</p>
            </div>
            
            <div className="bg-primary bg-opacity-10 rounded-lg p-6">
              <h2 className="text-lg font-medium text-gray-900">Wishlist</h2>
              <p className="mt-2 text-gray-600">View your saved items</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
} 